.standard {
  &__item-img {
    width: 450px;
    height: 300px;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.6);
  }
  background-color: #f5f5f5;
  &__inner {
    display: flex;
    justify-content: space-between;
    padding-top: 90px;
    padding-bottom: 130px;
  }

  &__item:nth-child(1) {
    width: 473px;
  }

  &__item-title {
    color: #212121;
    font-size: 20px;
    font-weight: 700;
    margin-top: 17px;
    margin-bottom: 60px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      width: 64px;
      height: 2px;
      background-color: #fe9042;
      bottom: -30px;
      left: 0;
    }
  }

  &__item-text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    & + & {
      margin-top: 25px;
    }
  }
}
