.stylish {
  background-color: #9a9a9a;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 74px 0 22px;
  }

  &__title {
    color: #ffffff;
    font-family: Raleway;
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 65px;
    position: relative;
    &::after{
      content: '';
      width: 74px;
      height: 2px;
      position: absolute;
      background-color: #fff;
      bottom: -33px;
      left: 174px;
    }
  }

  &__text {
    opacity: 0.8;
    color: #ffffff;
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 33px;
  }

  &__btn {
    color: #ffffff;
    font-size: 15px;
    font-weight: 400;
    border: 1px solid #fff;
    padding: 15px 30px;
    margin-bottom: 24px;
    border-radius: 3px;
    box-shadow: 0 0 1px #000;
    transition: all .3s;
    cursor: pointer;
    &:hover{
      box-shadow: 0 0 5px #000;
      background-color: rgb(243, 57, 10);
      color: #000;
      font-weight: 600;
      border: 1px solid  rgb(243, 150, 10);
    }
  }

  &__subtext {
    opacity: 0.22;
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
  }
}



