.cost {
  &__top {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    padding-top: 108px;
  }

  &__top-title {
    color: #212121;
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 60px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 64px;
      height: 2px;
      background-color: #ffa362;
      bottom: -30px;
      left: 514px;
    }
  }

  &__top-text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 55px;
  }

  &__inner {
    display: flex;
    justify-content: space-between;
    padding-bottom: 115px;
  }

  &__item {
    width: 273px;
  }
  &__item-chat {
    background: #f5f5f5;
    position: relative;
    border-radius: 4px;
    &::before {
      content: "";
      position: absolute;
      border: 10px solid transparent;
      border-top: 20px solid #f5f5f5;
      bottom: -30px;
      left: 26px;
    }
  }
  &__item-text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    font-style: italic;
    padding: 24px 12px;
  }

  &__item-author {
    color: #333333;
    font-size: 20px;
    font-weight: 700;
    padding-left: 85px;
    margin-bottom: 8px;
    position: relative;
    margin-top: 40px;
    &::before {
      content: "";
      width: 70px;
      height: 70px;
      position: absolute;
      background-image: url("../img/avatar_2_u231.png");
      left: 0;
      top: -11px;
    }
  }

  &__item-work {
    color: #86878b;
    font-size: 14px;
    padding-left: 85px;
    font-weight: 400;
  }
}
