.options {
  background-color: #f5f5f5;
  &__inner-top {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &__inner-top--title {
    color: #212121;
    font-size: 36px;
    font-weight: 400;
    text-transform: uppercase;
    margin-bottom: 62px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 64px;
      height: 2px;
      background-color: #ff8b38;
      bottom: -30px;
      left: 117px;
    }
  }

  &__inner-top--text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 80px;
  }

  &__inner {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    text-align: center;
    padding-bottom: 96px;
  }

  &__item {
    width: 345px;
    background-color: #333333;
    & + & {
      margin-left: 40px;
    }

  }

  &__item-text {
    color: #ffffff;
    font-family: "Open Sans";
    font-size: 20px;
    font-weight: 700;
    background-color: #333333;
    border-bottom: 1px solid #fff;
    padding: 21px 0;
  }

  &__item-price {
    font-size: 35px;
    color: #ffffff;
    font-weight: 700;
    padding: 30px 20px;
  }

  &__item-descr {
    opacity: 0.5;
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    font-style: italic;
    border-top: 1px solid #fff;
    padding: 18px 0;
  }

  &__item-line {
    height: 5px;
    background-color: #ff8b38;
  }

  &__item-link {
    background-color: #fff;
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    display: flex;
    flex-direction: column;
    padding: 14px;
    border-bottom: 1px solid #f5f5f5;
    &:hover {
      color: #ffffff;
      font-size: 14px;
      background-color: #ff8b38;
      font-weight: 700;
    }

    &:last-child {
      border: none;
    }
  }


}
.btn {
  color: #ffffff;
  font-size: 14px;
  background-color: #ff8b38;
  font-weight: 700;
}
