.tabs-wrapper {
  display: flex;
  flex-direction: column;
}
.tabs {
  background-color: #f5f5f5;
}
.container-tabs {
  width: 1100px;
  display: flex;
  align-items: center;
  padding-top: 145px;
  padding-bottom: 114px;
}
.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 112px;
  height: 112px;
  border: 1px solid transparent;
  cursor: pointer;
  color: #fff;
  background-color: #333333;
  & + & {
    margin-top: 1px;
  }
}
.tab.active {
  background-color: #ff8b38;
}
.tabs-panels {
  width: 480px;
  margin-left: 100px;
}
.tab-panel {
  display: none;
}
.tab-panel.active {
  display: block;
}

.tab-panel__title {
  color: #000000;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 50px;
}
.tab-panel__text {
  color: #86878b;
  font-size: 14px;
  font-weight: 400;
  & + & {
    margin-top: 20px;
  }
}

.tabs-btn {
  color: #ffffff;
  font-size: 15px;
  font-weight: 600;
  padding: 15px 25px;
  background-color: #ff8b38;
  border-radius: 4px;
  margin-top: 50px;
}
.tab-1{
  border-radius: 5px 5px 0 0;
}
.tab-2{
  border-radius: 0 0 5px 5px;
}
.text{
  position: relative;
}
.text::before {
  content: '';
  position: absolute;
  background-image: url('../img/image_right_u255.png');
  top: -70px;
  right: -405px;
  width: 320px;
  height: 253px;
}
