.contact {
  &__top {
    padding-top: 100px;
    text-align: center;
  }

  &__title {
    color: #212121;
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 60px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      background-image: url("../img/divider_orange_u7_copy_2.png");
      width: 64px;
      height: 2px;
      bottom: -30px;
      right: 520px;
    }
  }

  &__text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 80px;
  }

  &__forms {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-bottom: 80px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    width: 530px;
  }
  &__input {
    border-bottom: 1px solid #cecece;
    padding: 10px 13px;
    & + & {
      margin-top: 30px;
    }
    &::placeholder {
      color: #bdbdbd;
      font-size: 14px;
      font-weight: 400;
    }
  }

  &__message {
    border-bottom: 1px solid #cecece;
  }

  &__textareea {
    height: 170px;
    width: 530px;
    padding: 10px 13px;
    resize: none;
    &::placeholder {
      color: #bdbdbd;
      font-size: 14px;
      font-weight: 400;
    }
  }
  &__button {
    text-align: center;
  }
  &__btn {
    color: #ffffff;
    font-size: 15px;
    font-weight: 600;
    padding: 15px 20px;
    margin-bottom: 80px;
    border-radius: 3px;
    box-shadow: 0 0 1px #000;
    background-color: #ff8b38;
    transition: all 0.3s;
    cursor: pointer;
    border: 1px solid transparent;
    &:hover {
      box-shadow: 0 0 5px #000;
      background-color: rgb(243, 57, 10);
      color: #000;
      font-weight: 600;
      border: 1px solid rgb(243, 150, 10);
    }
  }
}
