.social {
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__items {
    display: flex;
    a {
      padding-left: 20px;
    }
  }
  &__item {
    width: 50px;
  }

  &__item-title {
    color: #333333;
    font-size: 20px;
    font-weight: 700;
    margin: 22px 0 12px;
  }

  &__item-text {
    color: #86878b;
    font-family: "Open Sans";
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 22px;
  }
}
