.sub {
  img {
    width: 450px;
    height: 300px;
    box-shadow: 0px 5px 10px 2px rgba(34, 60, 80, 0.6);
  }
  &__img {
    padding-top: 127px;
    padding-bottom: 166px;
  }
  &__wrapper {
    display: flex;
  }

  &__inner {
    width: 430px;
    margin: 107px 0 0 190px;
  }

  &__inner-top--title {
    color: #212121;
    font-size: 20px;
    font-weight: 700;
    margin-bottom: 60px;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      width: 64px;
      height: 2px;
      background: #ff8b38;
      bottom: -30px;
      left: 0;
    }
  }

  &__inner-top--text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 46px;
  }

  &__inner-top--subtitle {
    color: #212121;
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 10px;
    padding-left: 70px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 40px;
      height: 40px;
      background-image: url("../img/2-layers.png");
      top: 0;
      left: 0;
    }
  }

  &__inner-top--subtext {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 42px;
    padding-left: 70px;
    &:last-child {
      margin: 0;
    }
  }
}
