.awesome {
  &__inner-top {
    padding-top: 108px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__inner-top--title {
    color: #212121;
    font-family: Raleway;
    font-size: 36px;
    font-weight: 400;
    margin-bottom: 62px;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      width: 64px;
      height: 2px;
      background-color: #ff8b38;
      bottom: -30px;
      left: 165px;
    }
  }

  &__inner-top--text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    margin-bottom: 80px;
  }

  &__inner {
    display: flex;
    justify-content: space-around;
    padding-bottom: 100px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 350px;
  }

  &__item-title {
    color: #333333;
    font-size: 20px;
    font-weight: 700;
    margin-top: 29px;
    margin-bottom: 35px;
  }

  &__item-text {
    color: #86878b;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
  }
}
